.input {
  border-width: 1px;
  border-style: solid;
  border-color: #999;
  border-radius: .25rem;
  width: 100%;
  font-weight: 700;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  padding-left: .75rem;
  padding-right: .75rem;
}

.input:focus {
  -webkit-box-shadow: 0 0 2px 2px #6fffb0;
          box-shadow: 0 0 2px 2px #6fffb0;
  border-color: #6fffb0;
}

:global(.formfield) .input {
  border-width: 0;
  border-bottom-width: 1px;
  border-radius: 0;
  height: 2.5rem;
}

:global(.formfieldError) .input {
  border-color: #ff4040;
}

:global(.formfield) .input:focus,
:global(.formfieldError) .input:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  border-color: #6fffb0;
}
