/* Customize the label (the container) */

.container {
  display: block;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding-left: 2rem;
}

/* Hide the browser's default radio button */

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom radio button */

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  border-width: 2px;
  border-style: solid;
  border-color: #dadada;
  height: 1.5rem;
  width: 1.5rem;
}

.checkmark > svg {
  display: none;
  color: #7d4cdb;
}

.inverse .checkmark > svg {
  color: #f8f8f8;
}

/*
 * RadioButton 
 * - Rounded 50%
 * - Fill svg
 */

.radio {
  border-radius: 50%;
}

.radio > svg {
  fill: currentColor;
}

/*
 * CheckBox 
 * - Rounded 4px
 * - Stroke svg
 */

.checkbox {
  border-radius: .25rem;
}

.checkbox {
  stroke: currentColor;
}

/* When checked display it */

.container input:checked ~ .checkmark {
  border-color: #7d4cdb;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.inverse.container input:checked ~ .checkmark {
  border-color: #f8f8f8;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.container input:checked ~ .checkmark > svg {
  display: block;
}

/* Focus effects */

.container input:focus ~ .checkmark {
  border-color: #6fffb0;
  -webkit-box-shadow: 0 0 2px 2px #6fffb0;
          box-shadow: 0 0 2px 2px #6fffb0;
}

/* Hover effects */

.container:hover input ~ .checkmark {
  border-color: #333;
  -webkit-box-shadow: none;
          box-shadow: none;
}
