/* https://github.com/carloscabo/pure-css-select-style/ */

.container {
  width: 100%;
  background: #fff url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2211%22%20height%3D%2211%22%20viewBox%3D%220%200%2011%2011%22%3E%3Cpath%20d%3D%22M4.33%208.5L0%201L8.66%201z%22%20fill%3D%22%23666%22%2F%3E%3C%2Fsvg%3E") right 2px center no-repeat;
}

.selectBlockError {
  background-color: #fcc;
}

.select {
  width: 100%;
  height: 2.5rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-right: 1rem;
  border-width: 0;
  margin: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: transparent;
  background-image: none;
  background-image: none;
  outline: none;
  cursor: pointer;
  text-indent: .01px;
  text-overflow: ellipsis;
}

.select:focus {
  border-color: #6fffb0;
  -webkit-box-shadow: 0 0 2px 2px #6fffb0;
          box-shadow: 0 0 2px 2px #6fffb0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 0;
}

:global(.formfield) .select {
  border-bottom-width: 1px;
  border-style: solid;
  border-color: #999;
  border-radius: 0;
  font-weight: 700;
  height: 2.5rem;
  padding-left: .75rem;
  padding-right: .75rem;
}

:global(.formfieldError) .select {
  border-color: #ff4040;
}

:global(.formfield) .select:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
  border-color: #6fffb0;
}

.selectBlock:focus {
  background-color: #6fffb0;
  -webkit-box-shadow: none;
          box-shadow: none;
}
